import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { MdSettings, MdUndo, MdDirectionsRun } from 'react-icons/md';
import { FaFile, FaMousePointer, FaPlus, FaDoorOpen} from 'react-icons/fa';
import { GiBrickWall , GiDungeonGate } from "react-icons/gi";
import ToolbarButton from './toolbar-button';
import ToolbarSaveButton from './toolbar-save-button';
import ToolbarLoadButton from './toolbar-load-button';
import If from '../../utils/react-if';
import {
  MODE_IDLE,
  MODE_3D_VIEW,
  MODE_3D_FIRST_PERSON,
  MODE_VIEWING_CATALOG,
  MODE_CONFIGURING_PROJECT,
  MODE_WAITING_DRAWING_LINE,
  MODE_DRAWING_HOLE
} from '../../constants';
import * as SharedStyle from '../../shared-style';

const iconTextStyle = {
  fontSize: '19px',
  textDecoration: 'none',
  fontWeight: 'bold',
  margin: '0px',
  userSelect: 'none'
};

const Icon2D = ( {style} ) => <p style={{...iconTextStyle, ...style}}>2D</p>;
const Icon3D = ( {style} ) => <p style={{...iconTextStyle, ...style}}>3D</p>;

const ASIDE_STYLE = {
  backgroundColor: SharedStyle.PRIMARY_COLOR.main,
  padding: '10px'
};

const sortButtonsCb = (a, b) => {
  if (a.index === undefined || a.index === null) {
    a.index = Number.MAX_SAFE_INTEGER;
  }

  if (b.index === undefined || b.index === null) {
    b.index = Number.MAX_SAFE_INTEGER;
  }

  return a.index - b.index;
};

const mapButtonsCb = (el, ind) => {
  return (
    <If
      key={ind}
      condition={el.condition}
      style={{ position: 'relative' }}
    >
      {el.dom}
    </If>
  );
};

export default class Toolbar extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      currentSelectedElement: {}
    };
  }

  // shouldComponentUpdate(nextProps, nextState) {
  //   return this.props.state.mode !== nextProps.state.mode ||
  //     this.props.height !== nextProps.height ||
  //     this.props.width !== nextProps.width ||
  //     this.props.state.alterate !== nextProps.state.alterate ||
  //     this.state.currentSelectedElement !== nextState.currentSelectedElement
  //     ;
  // }

  catalogQuickSelect(element) {
    // clear current snaped element
    this.context.projectActions.rollback()
    // deselect all elements before selecting a new tool
    this.context.projectActions.unselectAll()
    switch (element['prototype']) {
      case 'lines':
        this.context.linesActions.selectToolDrawingLine(element['name']);
        break;
      case 'items':
        this.context.itemsActions.selectToolDrawingItem(element['name']);
        break;
      case 'holes':
        this.context.holesActions.selectToolDrawingHole(element['name']);
        break;
    }

    this.context.projectActions.pushLastSelectedCatalogElementToHistory(element);
  }

  render() {

    let {
      props: { state, width, height, toolbarButtons, allowProjectFileSupport, catalog },
      context: { projectActions, viewer3DActions, translator }
    } = this;
    let mode = state.get('mode');
    let alterate = state.get('alterate');
    let catalogList = catalog.elements;
    let currentSelectedElement =
    [...state.get('selectedElementsHistory').values()][0]
    // this.setState({currentSelectedElement})
    // let currentSelectedElement = []
    let alterateColor = alterate ? SharedStyle.MATERIAL_COLORS[500].orange : '';
    // Catalog Quick select elements
      let wallElement = catalogList['wall']
      let gateElement = catalogList['gate']
      let doorElement = catalogList['door']


    let sorter = [
      {
        index: 0, condition: allowProjectFileSupport, dom: <ToolbarButton
          active={false}
          tooltip={translator.t('New project')}
          onClick={event => confirm(translator.t('Would you want to start a new Project?')) ? projectActions.newProject() : null}>
          <FaFile />
        </ToolbarButton>
      },
      {
        index: 1, condition: allowProjectFileSupport,
        dom: <ToolbarSaveButton state={state} />
      },
      {
        index: 2, condition: allowProjectFileSupport,
        dom: <ToolbarLoadButton state={state} />
      },
      {
        index: 3, condition: true,
        dom: <ToolbarButton
          active={[MODE_VIEWING_CATALOG].includes(mode)}
          tooltip={translator.t('Open catalog')}
          onClick={event => projectActions.openCatalog()}>
          <FaPlus />
        </ToolbarButton>
      },
      {
        index: 4, condition: true, dom: <ToolbarButton
          active={[MODE_3D_VIEW].includes(mode)}
          tooltip={translator.t('3D View')}
          onClick={event => viewer3DActions.selectTool3DView()}>
          <Icon3D />
        </ToolbarButton>
      },
      {
        index: 5, condition: true, dom: <ToolbarButton
          active={[MODE_IDLE].includes(mode)}
          tooltip={translator.t('2D View')}
          onClick={event => projectActions.setMode( MODE_IDLE )}>
          {[MODE_3D_FIRST_PERSON, MODE_3D_VIEW].includes(mode) ? <Icon2D style={{color: alterateColor}} /> : <FaMousePointer style={{color: alterateColor}} />}
        </ToolbarButton>
      },
      {
        index: 6, condition: true, dom: <ToolbarButton
          active={[MODE_3D_FIRST_PERSON].includes(mode)}
          tooltip={translator.t('3D First Person')}
          onClick={event => viewer3DActions.selectTool3DFirstPerson()}>
          <MdDirectionsRun />
        </ToolbarButton>
      },
      {
        index: 7, condition: true, dom: <ToolbarButton
          active={false}
          tooltip={translator.t('Undo (CTRL-Z)')}
          onClick={event => projectActions.undo()}>
          <MdUndo />
        </ToolbarButton>
      },
      {
        index: 8, condition: true, dom: <ToolbarButton
          active={[MODE_CONFIGURING_PROJECT].includes(mode)}
          tooltip={translator.t('Configure project')}
          onClick={event => projectActions.openProjectConfigurator()}>
          <MdSettings />
        </ToolbarButton>
      },
      {
        index: 9, condition: true, dom: <ToolbarButton
          active={[MODE_WAITING_DRAWING_LINE].includes(mode)}
          tooltip={'Wall'}
          onClick={(e) => this.catalogQuickSelect(wallElement)}>
          <GiBrickWall />
        </ToolbarButton>
      },
      {
        index: 10, condition: true, dom: <ToolbarButton
          active={!!([MODE_DRAWING_HOLE].includes(mode)
                  && currentSelectedElement &&
                  currentSelectedElement.name === 'gate')}
          tooltip={'Gate'}
          onClick={(e) => this.catalogQuickSelect(gateElement)}>
          <GiDungeonGate />
        </ToolbarButton>
      },
      {
        index: 11, condition: true, dom: <ToolbarButton
          active={!!([MODE_DRAWING_HOLE].includes(mode)
                  && currentSelectedElement &&
                  currentSelectedElement.name === 'door')}
          tooltip={'Door'}
          onClick={(e) => this.catalogQuickSelect(doorElement)}>
          <FaDoorOpen />
        </ToolbarButton>
      }
    ];

    sorter = sorter.concat(toolbarButtons.map((Component, key) => {
      return Component.prototype ? //if is a react component
        {
          condition: true,
          dom: React.createElement(Component, { mode, state, key })
        } :
        {                           //else is a sortable toolbar button
          index: Component.index,
          condition: Component.condition,
          dom: React.createElement(Component.dom, { mode, state, key })
        };
    }));

    return (
      <aside style={{ ...ASIDE_STYLE, maxWidth: width, maxHeight: height }} className='toolbar'>
        {sorter.sort(sortButtonsCb).map(mapButtonsCb)}
      </aside>
    )
  }
}

Toolbar.propTypes = {
  state: PropTypes.object.isRequired,
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  allowProjectFileSupport: PropTypes.bool.isRequired,
  toolbarButtons: PropTypes.array
};

Toolbar.contextTypes = {
  projectActions: PropTypes.object.isRequired,
  viewer2DActions: PropTypes.object.isRequired,
  viewer3DActions: PropTypes.object.isRequired,
  linesActions: PropTypes.object.isRequired,
  holesActions: PropTypes.object.isRequired,
  itemsActions: PropTypes.object.isRequired,
  translator: PropTypes.object.isRequired,
};
